import {useDispatch, useSelector} from "react-redux";
import {useEffect} from "react";
import {adminDeletePost, adminGetBlog} from "../../../services/adminService";

export default function MListUser() {
    const dispatch = useDispatch();
    const listPosts = useSelector(state => {
        return state.admin.admin
    });
    useEffect(() => {
        dispatch(adminGetBlog())
    }, []);
    return (
        <>
            <h1>Qun l bi vit </h1>
            <table className="table table-bordered">

                <thead>
                <tr>
                    <th scope="col">STT</th>
                    <th scope="col">ID</th>
                    <th scope="col">ID ngi dng</th>
                    <th scope="col">Trng thi</th>
                    <th scope="col">Lt thch</th>
                    <th scope="col">Thi gian</th>
                    <th scope="col">Tn ngi dng</th>
                    <th scope="col">Tiu </th>
                    <th scope="col">Ni dung</th>
                    <th scope="col">Thao tc</th>
                </tr>
                </thead>
                {listPosts?.map((item, index) => (
                    <tbody>
                    <tr>
                        <td>{index + 1}</td>
                        <td>{item.id}</td>
                        <td>{item.idU}</td>
                        <td>{item.status === 1 ? "Cng khai" : "Ch mnh ti"}</td>
                        <td>{item.like}</td>
                        <td>{item.time}</td>
                        <td>{item.name}</td>
                        <td>{item.title}</td>
                        <td>{item.content}</td>
                        <td>
                            <button className="btn btn-danger" onClick={()=>{
                             dispatch(adminDeletePost({id:item?.id}))
                            }}>Xa bi </button>
                        </td>
                    </tr>
                    </tbody>
                ))}
            </table>


        </>
    )
}